.container{
    color: white;
    overflow-x: hidden;
}

.title{
    text-align: center;
}

.data{
    width: 30%;
    display: flex;
    flex-direction: column;
    margin-right: 70%;
    padding: 12px;
}

.oddRow{
    background-color: rgb(30, 30, 30);
    border-radius: 8px;
    width: 100%;
}

.rightAlign{
    text-align: right;
}

.rightData{
    position: absolute;
    left: 40%;
    width: 45%;
}

.leftAlign{
    position: absolute;
}

.subText{
    margin: 24px;
    margin-top: 100px;
}