body{
  background-color: rgb(0, 0, 0);
  z-index: -1;
}
a{
  color: white;
  text-decoration: 1px underline dotted white;
  transition: 1s ease;
}

a:hover{
  color: rgb(0, 201, 107);
  text-decoration: 1px underline dotted rgb(0, 201, 107);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}