.container{
    margin-top: 20px;
    margin-bottom: 20px;
    background-color: rgb(30, 30, 30);
    border-radius: 8px;
    padding: 12px;
    transition: .5s ease;
}

.link{
    text-decoration: none;
}

.link:hover{
    text-decoration: none;
}

.img{
    position: absolute;
}

.container:hover{
    color: rgb(0, 201, 107);
    -moz-transform: scaleX(1.025);
    -o-transform: scaleX(1.025);
    -ms-transform: scaleX(1.025);
    transform: scale(1.5);
    cursor: pointer;
}

.name{
    margin-left: 96px;
}

.rate{
    position: absolute;
    right: 5%;
    top: 25%;
}