.bar{
    background-color: rgba(39, 39, 39, 255);
    z-index: 3;
    width: 100%;
    height: 65px;
    color: white;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: row;
}

.link{
    text-align: center;
    flex: 1;
    font-size: 31px;
    padding-top: 12px;
    text-decoration: none;
}

.link:hover{
    text-decoration: none;
    color: black;
    background-color: white;
}