.container{
    margin: 12px;
    border-radius: 4px;
    position: relative;
    background-color: rgb(30, 30, 30);
    justify-content: center;
    text-align: center;
    overflow: hidden;
}

.img{
    flex-shrink: 0;
    min-width: 100%;
    min-height: 100%;
    display: block;
    width: 100%;
    height: auto;
    position: inherit;
}

.container:hover .dropdown{
    bottom: 0;
    height: 100%;
}

.dropdown{
    width: 100%;
    height: 0;
    cursor: pointer;
    background-color: white;
    justify-content: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: black;
    text-align: center;
    left: 0;
    right: 0;
    top: 0;
    bottom: auto;
    overflow: hidden;
    position: inherit;
    float: none;
    transition: .5s ease;
}

.bigText{
    font-size: x-large;
}

.role{
    font-size: smaller;
}